@import '../../styles/variables';
@import '../../styles/mixins';

.user-form__container {
  max-width: 400px;
  margin-right: auto;
  margin-left: auto;
  padding: 4px 24px;
  @include md {
    padding: 8px;
  }
}

.user-form__input {
  margin-bottom: 24px;
  height: 42px;
  border: 1px solid $grey-600;
}

.user-form__title {
  font-size: 1.6rem;
  margin: 1rem 0 1.5rem;
  color: $grey-900;
  font-weight: 600;
  @include md {
    font-size: 1.8rem;
  }
  @include lg {
    font-size: 2rem;
  }
  @include xl {
    font-size: 2.2rem;
  }
}

.user-form__label {
  font-size: 12px;
  color: $grey-700;
  font-weight: 600;
  margin-bottom: 8px;
}

.user-form__btn {
  min-width: 80px;
  padding: 10px 12px;
  &:disabled {
    background-color: $grey-200;
    color: $grey-500;
    border: 1px solid $grey-300;
  }
}

.user-form__link {
  color: $grey-700;
  font-size: 13px;
  &:hover {
    color: $primary;
  }
}
